import * as React from 'react';
import ReactMarkdown from "react-markdown";
import './style.scss';

import dummyImage from '../../assets/images/dummy.png'
import LanguageIcon from '../../components/language-icon/LanguageIcon';
import data from '../../data/projects'

interface IProjectProps {
}

const Project: React.FunctionComponent<IProjectProps> = (props) => {
  const [projectDetail, setProjectDetail] = React.useState(data[0]);

  const seeDetail = React.useCallback(
    (idx) => {
      setProjectDetail(data[idx]);
    },
    [],
  )

  return (
    <>
      <div>
        <div className="heading">
          <h1>My Projects</h1>
        </div>
        <div className="row project-container">
          <div className="col card-container">
            {data.map((v, i) => (
              <div className="card" key={i}>
                <div className="card-img">
                  <picture>
                    <img src={v.image ? v.image : dummyImage} alt="project name" />
                  </picture>
                </div>

                <a href={`/project/${i}`} className="card-title" onClick={(e) => {
                  e.preventDefault();
                  seeDetail(i);
                }}>
                  {v.name}
                </a>
                <div className="card-description">
                  <ReactMarkdown>
                    {v.desc}
                  </ReactMarkdown>
                </div>
              </div>
            ))}
          </div>

          {/* detail */}
          <div className="col detail-container">
            <div className="detail-image">
              <picture>
                <img src={projectDetail.image ? projectDetail.image : dummyImage} alt={projectDetail.name} />
              </picture>
            </div>
            <div className="detail-description-container">
              <h2 className="detail-title">{projectDetail.name}</h2>
                <ReactMarkdown className="detail-project"> 
                  {projectDetail.desc}
                </ReactMarkdown>
              <h3 className="detail-subtitle">
                Story
              </h3>
                <ReactMarkdown className="detail-story">
                  {projectDetail.story}
                </ReactMarkdown>
              <h3 className="detail-subtitle">
                Technology
              </h3>
              <div className="detail-tech-container">
                {
                  projectDetail.tech.map((v, i) => <LanguageIcon name={v} key={i} />)
                }
              </div>
              <h3 className="detail-subtitle mobile-text" >
                See my other project
              </h3>
            </div>

            <div className="detail-action-container">
              <a href={projectDetail.site} target="_blank" rel="noreferrer" className={ projectDetail.site === 'none' ? "detail-action-button-visit not-active" : "detail-action-button-visit"}>
                {projectDetail.site === 'none' ? 'Sorry, no preview' :"See Demo"}
              </a>
              <a href={projectDetail.source} target="_blank" rel="noreferrer" className="detail-action-button-source">
                See Code
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Project;
